import React from 'react';

import PropTypes from 'prop-types';

import { CommonButton, LinkButton, ReactLinkButton } from './styles';

export const Button = React.forwardRef((props, ref) => {
    if (props.type === 'link') {
        return (
            <LinkButton ref={ref} data-testid={props.dataTestId} {...props}>
                {props.caption}
            </LinkButton>
        );
    }
    if (props.type === 'reactLink') {
        return (
            <ReactLinkButton ref={ref} data-testid={props.dataTestId} {...props}>
                {props.caption}
            </ReactLinkButton>
        );
    }
    return (
        <CommonButton ref={ref} data-testid={props.dataTestId} {...props}>
            {props.caption}
        </CommonButton>
    );
});

Button.propTypes = {
    type: PropTypes.oneOf(['button', 'submit', 'link', 'reactLink']),
    variant: PropTypes.oneOf(['primary', 'secondary', 'tertiary', 'error', 'danger', 'hidden', 'row']),
    size: PropTypes.oneOf(['tight', 'fit']),
    raised: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
    disabled: PropTypes.bool,
    isInverted: PropTypes.bool,
    href: PropTypes.string,
    to: PropTypes.string,
    target: PropTypes.string,
    title: PropTypes.string,
    tabIndex: PropTypes.string,
    form: PropTypes.string, //  ,      submit  id  form,     form (-  )
    caption: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    dataTestId: PropTypes.string, //  html  data-testid (babel plugin "react-remove-properties")
    negativeMargin: PropTypes.bool, //   row-  
    onClick: PropTypes.func,
    textCenter: PropTypes.bool,
};
